@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700;800;900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Reem+Kufi+Ink&display=swap');




:root {

    --primary1-light: #FAF7E0;
    --primary1: #FFEC3F;
    --primary3: #FEE500;
    --primary2-dark1: #1976D2;
    --primary2-dark2: #209BFF;
    --primary2-dark3: #3DBDFF;
    --primary2-light2: #F8F8F8;
    --special-black: #1c1c1c;
    --special-black-light:#363636;
}

.App {
    text-align: center;
    width: 100%;
}

#root{
  width:100%;
  font-family: 'Inter', sans-serif;
  height: 100%;
  color: var(--special-black);
}

.App-link {
    color: #61dafb;
}

/*NAV BAR*/
header {
    all: unset;
}

.nav {
    padding: 10px;
    display: flex;
    width: 100%;
    box-sizing: border-box;
    height: 60px;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    box-shadow: 0px 0.000015px 3px 2px rgba(0, 0, 0, 0.234);
    z-index: 1;
    background-color: var(--primary1);
    position: sticky;
    top: 0;
}
.responsiveNav{
  position:fixed;
  top:0px;
  z-index: 400;
  background:var(--primary1);
  width:100vw;
  height:fit-content;
  transition: ease-in-out 0.5s;
}


#close {
    all: unset;
    z-index: 400;
    width: 40px;
    padding: 5px 10px;
    height: fit-content;
    position: absolute;
    top: 5px;
    right: 10px;
    opacity:0.5;
}

#close:hover {
    color: var(--primary1);
    cursor: pointer;
    opacity:1;
}

.sns-logo {
    display: flex;
    gap: 20px;
    margin: auto;

    padding-top: 50px;
    align-items: center;
    justify-content: center;
}

.sns-logo i{
  color:var(--special-black);
  font-size: 30px;
  opacity: 0.4;
  text-decoration: none;

}

.sns-logo i:hover {
    color:var(--special-black);
    opacity:1
}

.responsiveEl {
    margin-top: 7rem;
    margin-bottom: 10rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 2rem;
}


.responsiveEl a {
    font-size: 3rem;
    text-decoration: none;
    color: var(--special-black);
    text-transform: uppercase;
    font-weight: 600;
    opacity: 0.6;
}

.responsiveNav a:hover {
    opacity: 1;
}


.navRight {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-right: 200px;
}

.navRight .icon {
    display: none;

}

.nav-sns {
    margin-right: 30px;
    top: 38%;
    right: 20%;
    font-size: 12px;
}

#commission {
    font-size: 12px;
    background-color: var(--primary3);
    padding: 7px 10px;
    border-radius: 10px;
    box-shadow: 1px 2px 14px -7px rgba(0, 0, 0, 0.57);
    -webkit-box-shadow: 1px 2px 10px -7px rgba(0, 0, 0, 0.57);
    -moz-box-shadow: 1px 2px 14px -7px rgba(0, 0, 0, 0.57);
    transition: 0.2s ease-in-out;
    opacity: 0.5;
}

#commission:hover {
    opacity: 1;
    transition: 0.2s ease-in-out;
}

#commission a {
    color: var(--special-black);
    transition: 0.1s ease-in-out;

}

#commission a:hover {
    transition: 0.1s ease-in-out;

}

.wepo-logo {
    width: 100px;
    margin-left: 50px;
}

.nav .link {
    text-decoration: none;
    color: var(--special-black);
    opacity: 1;
    transition: 0.2s ease-in;
    padding: 10px;
}

.nav .linkContainer {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.inputField {
    width: 300px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 10px;
}

#demo-radio-buttons-group-label {
    margin: 0px;
    padding: 0px;
    font-size: 1.5rem;
    font-weight: 600;
}

.okform .radioN {
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin:auto;
  margin-top: 20px;
}
/*FORM CONTAINER*/

.MuiFormGroup-root {
    margin: 0px;
    padding: 0px;
    font-size: 12px;
}

.okform{
  position:fixed;
  width: 100%;
  height:100%;
  display:flex;
  justify-content: center;
  align-items: center;
  top:0;
  z-index: 10;
  background: rgba(255, 225, 33, 0.98);
}

.okform .inputField{
  display: flex;
  justify-content: center;
  margin:auto;

}

.okform{
  background:'red';
  z-index: 100;
}
